<template>
  <div>
    <!-- Question pack button group -->
    <b-button-toolbar v-if="!questionsEditing" class="my-2">
      <b-button-group>
        <b-btn size="sm"
               v-b-modal.addQuestionPackModal>Добавить</b-btn>
      </b-button-group>
    </b-button-toolbar>

    <!-- Questions view button group -->
    <b-button-toolbar v-if="questionsEditing" class="my-2">
      <b-button-group>
        <b-btn size="sm"
               @click="showQuestionEditorModal(null)">
          Добавить новый вопрос
        </b-btn>
        <b-btn size="sm"
               variant="outline-secondary"
               @click="questionsEditing = false">
          Назад
        </b-btn>
      </b-button-group>
    </b-button-toolbar>

    <!-- Question packs table -->
    <b-table striped
             :fields="fields"
             :items="question_packs"
             v-if="!questionsEditing">
      <template slot="title" slot-scope="row">
        <a href="#" @click="questionsEditing = !questionsEditing; getQuestionsForEditor(row.item)">{{ row.item.title }}</a>
      </template>
      <template slot="actions" slot-scope="row">
        <div class="text-sm-right">
          <b-button size="sm" @click.stop="getQuestionCount(row.item); row.toggleDetails()"><i class="fas fa-info-circle" aria-hidden="true"></i></b-button>
          <click-confirm>
            <b-button size="sm" @click.stop="deleteQuestionPack(row.item)"><i class="fas fa-trash" aria-hidden="true"></i></b-button>
          </click-confirm>
        </div>
      </template>
      <template slot="row-details" slot-scope="row">
        <b-card>
          <b-row class="mb-2">
            <b-col sm="3" class="text-sm-right"><b>Вопросов:</b></b-col>
            <b-col sm="3" v-text="row.item.questions"></b-col>
            <b-col class="text-sm-right col-sm">
              <b-button size="sm" @click="row.item.editing = !row.item.editing"><i class="fas fa-edit" aria-hidden="true"></i></b-button>
            </b-col>
          </b-row>
          <b-row v-if="row.item.editing">
            <b-input-group prepend="Новое название">
              <b-form-input v-model="row.item.title"></b-form-input>
              <b-input-group-append>
                <b-btn variant="success" @click="saveQuestionPack(row.item)"><i class="far fa-save"></i></b-btn>
              </b-input-group-append>
            </b-input-group>
          </b-row>
        </b-card>
      </template>
    </b-table>

    <!-- Questions table -->
    <b-table striped
             :fields="questionEditorFields"
             :items="questionEditorItems"
             v-else-if="questionsEditing">
      <template slot="question" slot-scope="row">
        <div class="pr-2">
          <a href="#"
             @click="showQuestionEditorModal(row.item)"
             v-html="row.item.question"
             class="question">
          </a>
          <b-badge pill>{{ row.item.type }}</b-badge>
        </div>
      </template>
      <template slot="actions" slot-scope="row">
        <div class="text-sm-right">
          <b-button size="sm" @click.stop="row.toggleDetails"><i class="fas fa-info-circle" aria-hidden="true"></i></b-button>
          <click-confirm>
            <b-button size="sm" @click.stop="deleteQuestion(row.item)"><i class="fas fa-trash" aria-hidden="true"></i></b-button>
          </click-confirm>
        </div>
      </template>
      <template slot="row-details" slot-scope="row">
        <question-card :question="row.item"></question-card>
      </template>
    </b-table>

    <!-- Add question pack modal -->
    <b-modal id="addQuestionPackModal"
             ref="addQuestionPackModal"
             title="Добавить новый сборник вопросов"
             ok-title="Сохранить"
             cancel-title="Отмена"
             @ok="handleAddQuestionPackSubmit"
             @show="newQuestionPackName = ''"
             v-if="!questionsEditing">
      <b-form @submit.stop.prevent="handleAddQuestionPackSubmit">
        <b-input-group size="sm" prepend="Название">
          <b-form-input v-model="newQuestionPackName"></b-form-input>
        </b-input-group>
      </b-form>
    </b-modal>

    <!-- Add/edit question modal -->
    <b-modal id="addEditQuestionModal"
             ref="addEditQuestionModal"
             title="Редактирование вопроса"
             v-if="questionsEditing"
             ok-title="Сохранить"
             cancel-title="Отмена"
             @ok="() => { this.$refs.questionEditor.addSaveQuestion() }"
             size="lg">
      <question-editor :question_pack_id="questionPackForEdit._id"
                       ref="questionEditor"
                       :question="newQuestion"
                       @updated="questionUpdated"
                       @added="newQuestionAdded"></question-editor>
    </b-modal>
  </div>
</template>
<script>
import Axios from 'axios'
import Authentication from '@/components/pages/Authentication'
import QuestionCard from './QuestionCard'
import QuestionEditor from './QuestionEditor'
import ClickConfirm from './ClickConfirm'
import { mapGetters } from 'vuex'

const TestManagerAPI = `http://${window.location.hostname}:3001`

const defaultQuestion = {
  question: '',
  type: 'выбор',
  choise_answers: [
    { answer: '', correct: false },
    { answer: '', correct: false }
  ]
}

export default {
  components: {
    'question-card': QuestionCard,
    'question-editor': QuestionEditor,
    'click-confirm': ClickConfirm
  },

  data () {
    return {
      newQuestionPackName: '',

      needUpdateQuestionCounts: false,
      questionsEditing: false,
      questionPackForEdit: {},
      questionEditorFields: [
        { key: 'question', label: 'Вопрос' },
        { key: 'actions', label: 'Действия' }
      ],
      questionEditorItems: [],
      newQuestion: JSON.parse(JSON.stringify(defaultQuestion))
    }
  },

  props: ['fields'],

  computed: {
    ...mapGetters({
      'question_packs': 'questionPack/questionPacks'
    })
  },

  mounted () {
    this.$store.dispatch('questionPack/GET_QUESTION_PACKS')
  },

  methods: {
    saveQuestionPack (item) {
      this.$store.dispatch('questionPack/SAVE_QUESTION_PACK', item)
      item.editing = false
    },

    addQuestionPack () {
      this.$store.dispatch('questionPack/ADD_QUESTION_PACK', this.newQuestionPackName)
      this.$refs.addQuestionPackModal.hide()
    },

    deleteQuestionPack (item) {
      this.$store.dispatch('questionPack/DELETE_QUESTION_PACK', item)
    },

    handleAddQuestionPackSubmit (event) {
      event.preventDefault()
      if (!this.newQuestionPackName) alert('Поле не должно быть пустым')
      else this.addQuestionPack()
    },

    getQuestions (item) {
      return Axios.get(`${TestManagerAPI}/api/v1/question`, {
        params: {
          question_pack: item._id
        },
        headers: {
          'Authorization': Authentication.getAuthenticationHeader(this)
        }
      }).then(({data}) => {
        item.questions = data.length
        return data
      })
    },

    getQuestionCount (item) {
      if (this.needUpdateQuestionCounts || (item.questions === undefined)) return this.getQuestions(item)
    },

    getQuestionsForEditor (item) {
      if (this.questionPackForEdit !== item || this.needUpdateQuestionCounts) {
        this.questionPackForEdit = item
        this.getQuestions(item).then((data) => {
          this.questionEditorItems = this.dataParser(data, 'question', 'type', 'answers', '_id')
        })
      }
    },

    showQuestionEditorModal (question) {
      if (!question) this.newQuestion = JSON.parse(JSON.stringify(defaultQuestion))
      else {
        this.newQuestion = JSON.parse(JSON.stringify(question))
        if (this.newQuestion.type === 'выбор') this.newQuestion.choise_answers = this.newQuestion.answers
      }
      this.$refs.addEditQuestionModal.show()
    },

    newQuestionAdded (data) {
      this.questionEditorItems.push(data.question)
    },

    questionUpdated (data) {
      let index = this.questionEditorItems.findIndex((element) => {
        return element._id === data.question._id
      })
      if (index !== -1) this.questionEditorItems.splice(index, 1, data.question)
    },

    deleteQuestion (item) {
      console.log('test')
      return Axios.delete(`${TestManagerAPI}/api/v1/question`, {
        params: { question_id: item._id },
        headers: {
          'Authorization': Authentication.getAuthenticationHeader(this)
        }})
        .then(({data}) => {
          let index = this.questionEditorItems.indexOf(item)
          if (index !== -1) this.questionEditorItems.splice(index, 1)
        })
    },

    dataParser (targetedArray, ...options) {
      let parsedData = []
      targetedArray.forEach(item => {
        let parsedItem = {}
        options.forEach(option => (parsedItem[option] = item[option]))
        parsedData.push(parsedItem)
      })
      return parsedData
    }
  }
}
</script>
<style>
a.question p{
  display: inline-block;
  margin-bottom: 0;
}
</style>



